let _this;
import {request} from '@/api/reques.js';
import {goWindow} from '@/api/navigate.js'  // 页面跳转
import Brand from '@/pages/common/brand.vue';
import LetterList from "@/pages/common/letterList.vue";
export default{
	data(){
		return{
			show1:false,
			show2:false,
			car_type:[], // 车型
		}
	},
	created() {
		this.getSearchCondition()
		_this = this
	},
	watch:{
		"$store.state.cars":(v)=>{
			_this.handleBrands(v.brandId)
		}
	},
	methods:{
		handleModels(){
			this.show1 = !this.show1
		},
		handleBrand(){
			this.show2 = !this.show2
			goWindow("/pages/common/brand")
		},
		// 获取车型,排放,驱动
		getSearchCondition(){
			request("/index/getSearchCondition",{},"GET")
			.then((res)=>{
				//console.log(res)
				this.car_type = res.data.data.car_type.slice(1); // 车型
			})
		},
		// 根据车型筛选
		handleCarType(index){
			let carId = index + 1;
			this.$emit('handleCarType',carId);
			this.show1 = false;
		},
		// 根据品牌筛选
		handleBrands(brandId){
			this.$emit('handleBrands',brandId);
			this.show2 = false;
		},
	},
	components:{
		Brand,
		LetterList
	}
}
